import { useEffect, useRef, useState } from "react";
import { Drawer, message } from 'antd';
import LogicFlow from "@logicflow/core";
import SchemaBuilder from '@xrenders/schema-builder';
import "@logicflow/core/dist/style/index.css";
import {
  DndPanel,
  SelectionSelect,
  Control,
  MiniMap
} from '@logicflow/extension';
import '@logicflow/extension/lib/style/index.css'
import './App.css';

LogicFlow.use(DndPanel);
LogicFlow.use(SelectionSelect);
LogicFlow.use(Control);
LogicFlow.use(MiniMap);

const App = () => {
  const refContainer = useRef();
  const [open, setOpen] = useState(false);
  const [schemaData, setSchemaData] = useState({});
  const [nodeId, setNodeId] = useState('');

  useEffect(() => {
    const logicflow = new LogicFlow({
      container: refContainer.current,
      grid: true,
      height: 1000,
    });

    logicflow.on("node:click", (data) => {
      setOpen(true)
      setNodeId(data?.data?.id)
    });

    logicflow.extension.control.addItem({
      key: 'mini-map',
      iconClass: "custom-minimap",
      title: "",
      text: "",
      onMouseEnter: (lf, ev) => {
        const position = lf.getPointByClient(ev.x, ev.y);
        lf.extension.miniMap.show(
          position.domOverlayPosition.x - 120,
          position.domOverlayPosition.y + 35
        );
      },
      onClick: (lf, ev) => {
        const position = lf.getPointByClient(ev.x, ev.y);
        lf.extension.miniMap.show(
          position.domOverlayPosition.x - 120,
          position.domOverlayPosition.y + 35
        );
      },
    });

    logicflow.extension.dndPanel.setPatternItems([
      {
        label: '',
        icon: 'data:image/png;base64,',
        callback: () => {
          logicflow.extension.selectionSelect.openSelectionSelect();
          logicflow.once('selection:selected', () => {
            logicflow.extension.selectionSelect.closeSelectionSelect();
          });
        }
      },
      {
        type: 'circle',
        text: '',
        label: '',
        icon: 'data:image/png;base64,',
      },
      {
        type: 'rect',
        label: '',
        icon: 'data:image/png;base64,',
        className: 'important-node'
      },
      {
        type: 'rect',
        label: '',
        icon: 'data:image/png;base64,',
        className: 'import_icon'
      },
      {
        type: 'diamond',
        label: '',
        icon: 'data:image/png;base64,',
      },
      {
        type: 'circle',
        text: '',
        label: '',
        icon: 'data:image/png;base64,',
      }
    ]);

    logicflow.render();

  }, []);

  const onClose = () => {
    setOpen(false)
  }

  return (
    <>
      <div className="App" ref={refContainer}>
      </div>
      <Drawer
        width={1000}
        placement="right"
        onClose={onClose}
        open={open}
      >
        <div style={{ height: '85vh' }}>
          {
            open && (
              <SchemaBuilder
                defaultValue={schemaData[nodeId]}
                importBtn={true}
                exportBtn={true}
                pubBtn={false}
                saveBtn={{
                  onClick: (schema) => {
                    schemaData[nodeId] = schema
                    setSchemaData(schemaData)
                    message.success('')
                    setOpen(false)
                  }
                }}
              />
            )
          }
        </div>
      </Drawer>
    </>
  );
}

export default App;
